.sign-selection {
    border: 2px solid #666;
    padding: 1.2em;
    width: 400px;
    margin-bottom: 1em;
}

.form-cnt {
    width: 600px;
    padding: 2em 0;
}

.signup input { 
    padding: .75em;
    border-bottom: 2px solid #999;
    /* padding-left: 50px; */
    font-size: 1.01em;
    box-sizing: border-box;
}

.signup .i-p:focus { 
    border-bottom: 2px solid var(--dark-pink);
}

.signup .i-b:focus { 
    border-bottom: 2px solid var(--dark-blue);
}

.signup select { 
    padding: .75em;
    font-size: 1.01em;
}

.signup input.day-field {
    width: 68px;
    margin-right: 15px;
    border: 1px solid #999;
}

.signup .month-field {
    width: 175px;
    border: 1px solid #999;
    /* border: none;
    border-bottom: solid 2px #999; */
    margin-right: 15px;
}

.signup .radio {
    width: 15px;
    height: 15px;
    border: 1.5px solid #999;
    border-radius: 50%;
}

.signup .selected-radio-m::before {
    content: "";
    position: absolute;
    width: 8px;
    height: 8px;
    background: var(--dark-blue);
    top: calc(50% - 4px);
    left: calc(50% - 4px);
    border-radius: 50%;
}

.signup .selected-radio-f::before {
    content: "";
    position: absolute;
    width: 8px;
    height: 8px;
    background: var(--dark-pink);
    top: calc(50% - 4px);
    left: calc(50% - 4px);
    border-radius: 50%;
}

.signup .selected-radio-m.radio {
    border: 1.5px solid var(--dark-blue);
}

.signup .selected-radio-f.radio {
    border: 1.5px solid var(--dark-pink);
}

.signup input.year-field {
    border: 1px solid #999;
    width: 105px;
}

.input-mrgn-bottom {
    margin-bottom: 1em;
}

.signup {
    padding: 2em;
    /* width: 45%; */
}

.signup h2 {
    font-size: 2em;
    margin-bottom: 1em;
}

.signup label {
    margin-bottom: 5px;
}

.signup label {
    margin-bottom: 5px;
}

.signup label.for-input {
    color: #000;
    margin-bottom: 0;
    position: absolute;
    top: 12px;
    left: 12px;
    /* font-size: 1em; */
    /* color: #999; */
    pointer-events: none;
    transition: all .2s ease-in;
    /* background: #fff; */
    /* margin-bottom: 5px; */
}

.signup input:valid ~ label.for-input,
.signup input:focus ~ label.for-input {
    /* transform: scale(0.9); */
    font-size: .8em;
    top: -10px;
}

.signup .input-cnt {
    margin-bottom: 2em;
}

.input-cnt > img {
    position: absolute;
    top: 30px;
    left: 5px;
    width: 25px;
}

.signup button {
    padding: .8em;
    margin-top: 2.5em;
}

.profile-pic {
    width: 200px;
    height: 200px;
    background: var(--blue);
    margin-right: 25px;
}

.change-pic {
    padding: .5em;
    border: 2px solid #999;
    margin-bottom: 1em;
}

.mrg-bot {
    margin-bottom: 1em;
}