.side-drawer {
    height: 100vh;
    width: 50%;
    max-width: 300px;
    z-index: 200;
    transform: translateX(-125%);
    transition: transform .2s ease-out;
    background: linear-gradient(0deg, var(--blue), var(--pink));
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: 2px 0px 5px 2px rgba(0, 0, 0, 0.5);
}

.open {
    transform: translateX(0%);
}

.side-drawer ul {
    font-size: 1.25em;
}

.side-drawer li {
    padding: .75em 0;
    margin: 0 1.75em;
}

.side-drawer li .img1 {
    width: 27px;
    margin-right: 13px;
}

.side-drawer li .img2 {
    width: 25px;
    margin-right: 15px;
}

.side-drawer li .img3 {
    width: 24px;
    margin-right: 16px;
}

.side-drawer li .img5 {
    width: 50px;
    margin-right: 10px;
}

.side-drawer li p {
    padding: .5em;
    transition: background-color .15s ease-in-out, border-radius .15s ease-in-out, color .15s ease-in-out;
}

.side-drawer li:hover p.pink-item {
    /* background: rgba(187, 143, 216); */
    background: var(--pink);
    color: white;
    border-bottom-left-radius: 15px;
    border-top-right-radius: 15px;
}

.side-drawer li:hover p.blue-item {
    /* background: rgba(162, 167, 244); */
    background: var(--blue);
    color: white;
    border-top-left-radius: 15px;
    border-bottom-right-radius: 15px;
}


